'use client'

export default function Home() {
  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-500 to-blue-600">
      <div className="text-center">
        <h1 className="text-4xl font-bold text-white mb-4">Dayflow</h1>
        <p className="text-lg text-blue-100 mb-8">Human Resource Management System</p>
        <div className="flex gap-4">
          <a
            href="/auth/login"
            className="px-8 py-3 bg-white text-blue-600 font-semibold rounded-lg hover:bg-gray-100 transition"
          >
            Sign In
          </a>
          <a
            href="/auth/register"
            className="px-8 py-3 bg-blue-700 text-white font-semibold rounded-lg hover:bg-blue-800 transition"
          >
            Sign Up
          </a>
        </div>
      </div>
    </div>
  )
}
